@charset "UTF-8";
/**
 * The following styles get applied both on the front of your site
 * and in the editor.
 *
 */
/* Aufgaben block */
.wp-block-us-aufgaben-block {
  display: flex;
  align-items: center;
  position: relative;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  color: tomato;
  font-size: 1.5rem;
  font-weight: bold;
}

.wp-block-us-aufgaben-block:before {
  content: " ";
  background-image: url("./task.svg");
  background-repeat: no-repeat;
  background-size: 40px 40px;
  height: 40px;
  width: 40px;
  margin-right: 10px;
}

